import axios from 'axios'
import tools from '@/common/tools.js'

export default {
  name: 'bindemail',
  data: function() {
    return {
      email: '',
      errortxt: '请您输入正确的邮箱地址',
      isShowError: false
    }
  },
  methods: {
    hideError() {
      this.isShowError = false
    },
    setEmail() {
      if (tools.validEmail(this.email)) {
        const url = '/Mobile/User/userinfo.html'
        axios.post(url, {
          email: this.email
        })
          .then((res) => {
            if (res.data.status === 1) {
              tools.showError(res.data.msg)
              this.$store.commit('MODIFY_EMAIL', this.email)
              this.$router.push({path: 'accountset'})
            } else {
              tools.showError(res.data.msg)
            }
          })
          .catch((error) => {
            console.log(error)
          })
      } else {
        this.isShowError = true
      }
    }
  }
}



